// colors
[data-dark="false"] {
  --primary: #7BAFD4;
  --secondary: #4B9CD3;
  --text: #13294B;
  --background: #ffffff;
  --background-alt: #F8F8F8;
  --light-gray: #E1E1E1;
  --gray: #808080;
  --dark-gray: #767676;
  --overlay: #00000020;
}
[data-dark="true"] {
  --primary: #7BAFD4;
  --secondary: #4B9CD3;
  --text: #ffffff;
  --background: #181818;
  --background-alt: #13294B;
  --light-gray: #404040;
  --gray: #808080;
  --dark-gray: #b0b0b0;
  --overlay: #ffffff10;
}

:root {
  // font families
  --title: "Libre Baskerville", sans-serif;
  --heading: "Open Sans", sans-serif;
  --body: "Open Sans", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;
}
